import '../assets/css/pages/login.css';

function ForgotPassword() {
    return (
        <section className="auth-form padding-60">
            <div className="container">
                <form>
                    <div className="form-box">
                        <div className="section-title varient-two">
                            <h4>Forgot Password</h4>
                            <p>Enter the email associated with your account and we'll send an email with instructions to reset your password.</p>
                        </div>
                        <div className="form-group">
                            <label htmlFor="email" className="form-label">Email</label>
                            <input type="email" className="form-control" id="email" placeholder="Enter your email" />
                        </div>
                        <div className="row form-submit-btn">
                            <div className="col-lg-6">
                                <button className="button button-blue w-100" type="submit">Send</button>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </section>
    )
}

export default ForgotPassword;